import { put, delay } from 'redux-saga/effects';
import axios from 'axios';
import axiosODM from '../../axios-odm';

import * as actions from '../actions/index';


export function* authUserSaga(action) {
    yield put(actions.authStart());
    const authData = {
        email: action.email,
        password: action.password,
        returnSecureToken: true
    }
    console.log(action.isExistingUser);
    let url = 'https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyAm6eOsqSTqXYM2iQ1MoEtrmCzuB7OCC5g';
    if(!action.isExistingUser){
        url = 'https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyAm6eOsqSTqXYM2iQ1MoEtrmCzuB7OCC5g';
    }
    try{
        const response = yield axios.post(url, authData);
        const expirationDate = yield new Date(new Date().getTime() + response.data.expiresIn * 1000);
        console.log(response.data);

        yield localStorage.setItem('token', response.data.idToken);
        yield localStorage.setItem('expirationDate', expirationDate);
        yield localStorage.setItem('userId', response.data.localId);
        yield localStorage.setItem('emailId', response.data.email);
        yield put(actions.authSuccess(response.data.idToken, response.data.localId, response.data.email));
        yield put(actions.checkAuthTimeout(response.data.expiresIn));

        yield put(actions.fetchDeliveryAddress(response.data.idToken, response.data.localId));
        yield put(actions.initializeCart(response.data.localId));
        yield put(actions.initializeWishlist(response.data.localId));
        yield put(actions.fetchProduct());



        // yield put(actions.fetchUserInfoStart());
        // url = "/userDetails/"+response.data.localId+"/userdata";
        // //   const response = yield axios.get( url+".json");
        // response = yield axiosODM.get( url+".json");

        // yield put(actions.fetchUserInfoSuccess(response.data["email"],response.data["name"],response.data["phoneNo"]));        
    }catch(error){
        yield put(actions.authFail(error.response.data.error));
    }
}

export function* logoutSaga(action) {
    yield localStorage.removeItem('token');
    yield localStorage.removeItem('expirationDate');
    yield localStorage.removeItem('userId');
    yield localStorage.removeItem('emailId');
    // yield put(actions.logoutSucceed());
}

export function* checkAuthTimeoutSaga(action) {
    yield delay(action.expirationTime * 21600);
    yield put(actions.authLogout());
}

export function* authCheckStateSaga(action) {
    
    const token = yield localStorage.getItem('token');
        if (!token) {
            yield put(actions.authLogout());
        } else {
            const expirationDate = yield new Date(localStorage.getItem('expirationDate'));
            if (expirationDate <= new Date()) {
                yield put(actions.authLogout());
            } else {
                console.log("BEFORE SIGN UP");
                const userId = yield localStorage.getItem('userId');
                const emailId = yield localStorage.getItem('emailId');
                yield put(actions.authSuccess(token, userId, emailId));
                yield put(actions.fetchUserInfo(token, userId));
                yield put(actions.fetchDeliveryAddress(token, userId));
                yield put(actions.checkAuthTimeout((expirationDate.getTime() - new Date().getTime()) / 1000 ));

                yield put(actions.initializeCart(userId));
                yield put(actions.initializeWishlist(userId));

            }   
        }
}