import { put } from 'redux-saga/effects';
import axios from '../../axios-odm';

import * as actions from '../actions/index';

export function* initializeCartSaga(action) {
    console.log('CART INITIALIZED');
    try {
        let url = "/userDetails/"+action.userId+"/cart";
        const response = yield axios.get(url+".json");

        let urlCartTotal = "/userDetails/"+action.userId+"/cartTotal";
        const responseCartTotal = yield axios.get(urlCartTotal+".json");

        let cart = [];
    
        for( let key in response.data){
            cart.push({
                ...response.data[key],
                id: key
            });
        }
        yield put(actions.initializeCartSuccess(cart));

        let cartTotal = Number(responseCartTotal.data);
        yield put(actions.updateCartContentNo(cartTotal));

    } catch (error) {
        console.log(error);
        yield put(actions.initializeCartFailed(error));
    }
}

export function* addProductToCartSaga(action) {
    console.log('PRODUCT ADDED TO CART');
    yield put(actions.addProductToCartStart());
    try {
        let url = "/userDetails/"+action.userId+"/cart/"+action.productCode;
        const response = yield axios.get(url+".json");

        let urlCartTotal = "/userDetails/"+action.userId+"/cartTotal";
        const responseCartTotal = yield axios.get(urlCartTotal+".json");

        let urlCart = "/userDetails/"+action.userId+"/cart";
        const responseCart = yield axios.get(urlCart+".json");

        // IF ADDING FIRST ITEM OF A UNIQUE ID
        if(response.data === null){
            let product = {
                units: 1,
                unitPrice: action.unitPrice,
                oldPrice: action.oldPrice,
                productName: action.productName
            };
            yield axios.put(url+".json", product);
            if(responseCartTotal.data===null){
                let cartTotal = 1;
                yield axios.put(urlCartTotal+".json", cartTotal);
                yield put(actions.updateCartContentNo(cartTotal));
            }
            else{
                let cartTotal = Number(responseCartTotal.data)+1;
                yield axios.put(urlCartTotal+".json", cartTotal);
                yield put(actions.updateCartContentNo(cartTotal));

            }
            
        }
        else if(response.data != null){
            let product = {
                units: Number(response.data.units)+1,
                unitPrice: action.unitPrice,
                oldPrice: action.oldPrice,
                productName: action.productName
            };
            yield axios.put(url+".json", product);
            let cartTotal = Number(responseCartTotal.data)+1;
            yield axios.put(urlCartTotal+".json", cartTotal);
            yield put(actions.updateCartContentNo(cartTotal));


        }
        
        let cart = [];
        for( let key in responseCart.data){
            cart.push({
                ...responseCart.data[key],
                id: key
            });
        }
        yield put(actions.initializeCartSuccess(cart));
        
    } catch (error) {
        console.log(error);
        yield put(actions.addProductToCartFailed());
        
    }
    yield put(actions.addProductToCartFinished());
}

export function* removeProductFromCartSaga(action) {
    console.log('PRODUCT REMOVED FROM CART');
    yield put(actions.removeProductFromCartStart());
    let urlCart = "/userDetails/"+action.userId+"/cart";
    
    try {

        let url = "/userDetails/"+action.userId+"/cart/"+action.productCode;
        const response = yield axios.get(url+".json");

        let urlCartTotal = "/userDetails/"+action.userId+"/cartTotal";
        const responseCartTotal = yield axios.get(urlCartTotal+".json");

        if(Number(responseCartTotal.data)>1){
            if(Number(response.data.units)>1){
                let product = {
                    units: Number(response.data.units)-1,
                    unitPrice: action.unitPrice,
                    oldPrice: action.oldPrice,
                    productName: action.productName
                };
                yield axios.put(url+".json", product);

                let cartTotal = Number(responseCartTotal.data)-1;
                yield axios.put(urlCartTotal+".json", cartTotal);
                yield put(actions.updateCartContentNo(cartTotal));

            }
            else if(Number(response.data.units)===1){
                let cartTotal = Number(responseCartTotal.data)-Number(response.data.units);
                yield axios.put(urlCartTotal+".json", cartTotal);
                yield put(actions.updateCartContentNo(cartTotal));

                yield axios.delete(url+".json");
            }
        }
        else if(Number(responseCartTotal.data)===1){
            yield axios.delete(url+".json");
            yield axios.delete(urlCartTotal+".json");
        }
    } catch (error) {
        console.log(error);
    }
    let cart = [];
    const responseCart = yield axios.get(urlCart+".json");
    for( let key in responseCart.data){
        cart.push({
            ...responseCart.data[key],
            id: key
        });
    }
    yield put(actions.initializeCartSuccess(cart));
    yield put(actions.removeProductFromCartFinished());
}

export function* deleteProductFromCartSaga(action){
    yield put(actions.deleteProductFromCartStart());

    try {

        let urlProduct = "/userDetails/"+action.userId+"/cart/"+action.productCode;
        const responseProduct = yield axios.get(urlProduct+".json");

        // let urlProductUnits = "/userDetails/"+action.userId+"/cart/"+action.productCode+"/units";
        // const responseUnits = yield axios.get(urlProductUnits+".json");

        let urlCartTotal = "/userDetails/"+action.userId+"/cartTotal";
        const responseCartTotal = yield axios.get(urlCartTotal+".json");
        let cartTotal = Number(responseCartTotal.data);
        
        let productTotalUnits = Number(responseProduct.data['units']);

        if(cartTotal === productTotalUnits){
            yield axios.delete(urlCartTotal+".json");
            yield put(actions.updateCartContentNo(0));

        }
        else{
            cartTotal = cartTotal - productTotalUnits;
            yield axios.put(urlCartTotal+".json", cartTotal);
            yield put(actions.updateCartContentNo(cartTotal));
        }
        
        yield axios.delete(urlProduct+".json");
        // yield axios.put(urlCartTotal+".json", cartTotal);
        // yield put(actions.updateCartContentNo(cartTotal));
        
        // let deleteTotal = Number(responseCartTotal.data);
        let cart = [];
        let urlCart = "/userDetails/"+action.userId+"/cart";
        const responseCart = yield axios.get(urlCart+".json");
        for( let key in responseCart.data){
            cart.push({
                ...responseCart.data[key],
                id: key
            });
        }
        yield put(actions.initializeCartSuccess(cart));

        
    } catch (error) {
        console.log(error);
        yield put(actions.deleteProductFromCartFailed());

    }
    yield put(actions.deleteProductFromCartFinished());


}

export function* orderProductsSaga(action){
    yield put(actions.orderProductsStart());

    try {
        const orderDetails = {
            products: action.products,
            addressDetails: action.deliveryAddress,
            userId: action.userId,
            time: new Date().toLocaleString()
        }
        let urlOrder = "/orders";
        yield axios.post(urlOrder+".json", orderDetails);
        console.log("Order Placed");
        // const responseProduct = yield axios.get(urlProduct+".json");

        let urlCart = "/userDetails/"+action.userId+"/cart";
        let urlCartTotal = "/userDetails/"+action.userId+"/cartTotal";
        yield axios.delete(urlCart+".json");
        yield axios.delete(urlCartTotal+".json");

        yield put(actions.initializeCartSuccess(null));
        yield put(actions.updateCartContentNo(0));




        
    } catch (error) {
        console.log(error);
        // yield put(actions.deleteProductFromCartFailed());

    }
    // yield put(actions.deleteProductFromCartFinished());


}