import React from 'react';

import classes from './CategorySelection.css';

const CategorySelection = props => {

    return (
        <div className={classes.Selection}>
            {/* <button onClick={props.clicked}>{props.categoryName}</button> */}
            <div className={classes.SelectionButton} onClick={props.clicked}>
                {props.categoryName}
            </div>
        </div>
    )
}

export default CategorySelection;