import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';

import classes from './Auth.css';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import LockIcon from '@material-ui/icons/Lock';
import EmailIcon from '@material-ui/icons/Email';
import * as actions from '../../store/actions/index';


const Auth = props => {

    const [ isExistingUser, setisExistingUser ] = useState(true);
    const [ emailAddress, setEmailAddress ] = useState("");
    const [ password, setPassword ] = useState("");
    const [ confirmPassword, setConfirmPassword ] = useState("");

    const userTypeCheckboxCheckedHandler = () => {
        setisExistingUser(!isExistingUser);
        if(isExistingUser)
            setConfirmPassword("");
    }

    // if( props.isAuthenticated ){
    //     props.onSetAuthRedirectPath('/');
    // }

    // const { isAuthenticated, onSetAuthRedirectPath } = props;
    // useEffect(() => {
    //     onSetAuthRedirectPath('/categories');
    // },[isAuthenticated,onSetAuthRedirectPath]);
    // console.log("COMPONENT RERENDERED");
    const emailInputChangedHandler = (event) => {
        setEmailAddress(event.target.value);
    }
    const passwordInputChangedHandler = (event) => {
        setPassword(event.target.value);
    }
    const confirmPasswordInputChangedHandler = (event) => {
        setConfirmPassword(event.target.value);
    }

    const submitHandler = () => {
        const pattern = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
        const isValid = pattern.test(emailAddress);

        if( isExistingUser===true && isValid && password.length >= 6){
            console.log('Form Submitted: ' , isValid);
            props.onAuth(emailAddress,password,isExistingUser);
        }
        else if( isExistingUser===false && isValid && password.length >= 6 && password === confirmPassword){
            console.log('Form Submitted: ' , isValid);
            props.onAuth(emailAddress,password,isExistingUser);
        }
        // props.onAuth('kherkatarybhaskar1@felix.com','loha9mo9',isExistingUser);
    }

    // let authRedirect = null;
    // if (props.isAuthenticated) {
    //     authRedirect = <Redirect to={props.authRedirectPath}/>
    // }
    // console.log(props.authRedirectPath);

    return (
        <div className={classes.LoginForm}>
            {/* {authRedirect} */}
            <div className={classes.Square}>
                <form>
                { !isExistingUser  ?   <div className={classes.FormGroup}>
                                                <div className={classes.Icon}>
                                                    <AccountCircleIcon/>
                                                </div>
                                                <input 
                                                    type="text" 
                                                    className={classes.InputField}
                                                    // onChange={}
                                                    placeholder="First Name"></input>
                                                <label>First Name</label>
                                            </div> 
                                        
                                    : null 
                    }
                    { !isExistingUser  ?   <div className={classes.FormGroup}>
                                                <div className={classes.Icon}>
                                                    <AccountCircleIcon/>
                                                </div>
                                                <input 
                                                    type="text" 
                                                    className={classes.InputField}
                                                    // onChange={}
                                                    placeholder="Last Name"></input>
                                                <label>Last Name</label>
                                            </div> 
                                        
                                    : null 
                    }
                    <div className={classes.FormGroup}>
                        <div className={classes.Icon}>
                            <EmailIcon/>
                        </div>
                        <input 
                            type="email" 
                            className={classes.InputField} 
                            onChange={emailInputChangedHandler}
                            placeholder="yourname@domain.com"></input>
                        <label>Email Address</label>
                    </div>
                    <div className={classes.FormGroup}>
                        <div className={classes.Icon}>
                            <LockIcon/>
                        </div>
                        <input 
                            type="password" 
                            className={classes.InputField} 
                            onChange={passwordInputChangedHandler}
                            placeholder="****************"></input>
                        <label>Password</label>
                    </div>
    
                    { !isExistingUser  ?   <div className={classes.FormGroup}>
                                                <div className={classes.Icon}>
                                                    <LockIcon/>
                                                </div>
                                                <input 
                                                    type="password" 
                                                    className={classes.InputField}
                                                    onChange={confirmPasswordInputChangedHandler}
                                                    placeholder="****************"></input>
                                                <label>Confirm Password</label>
                                            </div> 
                                        
                                    : null 
                    }
                    <div className={classes.AuthModeSelector}>
                        <input type="checkbox" checked={isExistingUser} onClick={userTypeCheckboxCheckedHandler}/>
                        <h5>Already a user</h5>
                    </div>
                    
                    <div className={classes.Button}>
                        <Link 
                            onClick={submitHandler}
                            style={{textDecoration: 'none', color: 'white', height: '100%', display: 'flex', alignItems: 'center', width: '100%', justifyContent: 'center'}}>
                            <p className={classes.AuthButton}>{ isExistingUser ? "LOG IN" : "SIGN UP"}</p>
                        </Link>
                    </div>
                    <div className={classes.ErrorMessage}>
                        { props.errorMessage ? <p>Invalid User Credentials</p> : null}
                    </div>
                </form>
            </div>

        </div>
    )
}

const mapStateToProps = state => {
    return {
        loading: state.auth.loading,
        errorMessage: state.auth.error,
        isAuthenticated: state.auth.token !== null,
        authRedirectPath: state.auth.authRedirectPath
    };
};


const mapDispatchToProps = dispatch => {
    return {
        onAuth: ( email, password, isSignup ) => dispatch( actions.auth( email, password, isSignup ) ),
        onSetAuthRedirectPath: ( path ) => dispatch(actions.setAuthRedirectPath(path))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Auth);