import React, { Suspense, useEffect } from 'react';
import { Route, Switch, withRouter, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';

import Layout from './components/Layout/Layout';
import Auth from './components/Auth/Auth';
import * as actions from './store/actions/index';
import AccountControl from './components/AccountControl/AccountControl';
// import UserInfo from './components/AccountControl/UserInfo/UserInfo';
import DeliveryAddress from './components/AccountControl/DeliveryAddress/DeliveryAddress';
import AddProduct from './components/AddProduct';
import AllProducts from './components/Pages/AllProducts/AllProducts';
import OrdersAndReturns from './components/Pages/OrdersAndReturns/OrderAndReturns';
import Wishlist from './components/Pages/Wishlist/Wishlist';
import Cart from './components/Pages/Cart/Cart';
// import Auth from './components/Auth/Auth';

const UserInfo = React.lazy(() => {
  return import('./components/AccountControl/UserInfo/UserInfo');
});

const App = (props) => {

  const { onTryAutoSignUp } = props;
  // onFetchProduct();

  useEffect(() => {
    console.log("APP RENDERED");
    onTryAutoSignUp();
    // console.log(props.userId);
    // if(props.userId!==null){
    //   props.onInitializeWishlist(props.userId);
    // }
    // onInitializeCart(props.userId);
    console.log("AFTER SIGN UP");
  },[onTryAutoSignUp]);

  let routes = (
    <Switch>
      <Route path='/user' exact render={(props) => <AccountControl {...props}/>} />
      <Route path='/order-returns' render={(props) => <OrdersAndReturns {...props}/>}/>
      <Route path='/wishlist' render={(props) => <Wishlist {...props}/>}/>
      <Route path='/user/user-info' render={(props) => <UserInfo {...props}/>}/>
      <Route path='/user/delivery-address' render={(props) => <DeliveryAddress {...props}/>}/>
      <Route path='/add-product' render={(props) => <AddProduct {...props}/>}/>
      <Route path='/cart' render={(props) => <Cart {...props}/>}/>
      <Route path='/' exact render={(props) => <AllProducts {...props}/>}/>
      <Redirect to='/'/>
    </Switch>
  );
  
  if(!props.isAuthenticated){
    routes = (
      <Switch>
        <Route path='/cart' />
        <Route path='/user' render={(props) => <Auth {...props}/>}/>
        <Route path='/order-returns' render={(props) => <Auth {...props}/>}/>
        <Route path='/wishlist' />
        <Route path='/add-product' render={(props) => <Auth {...props}/>}/>
        <Route path='/' exact render={(props) => <AllProducts {...props}/>}/>
        <Redirect to='/'/>
      </Switch>
    );
  }

  return (
    <div>
      <Layout>
        {/* <h1>HELLO EVERYONE</h1> */}
        <Suspense fallback={<p>Loading...</p>}>{routes}</Suspense>
      </Layout>
    </div>
  );
}

const mapStateToProps = state => {
  return {
    isAuthenticated: state.auth.token !== null,
    token: state.auth.token,
    userId: state.auth.userId,
    authenticated: state.auth.authenticated,
    name: state.userInfo.name,
    email: state.userInfo.email,
    phoneNo: state.userInfo.phoneNo,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onTryAutoSignUp: () => dispatch(actions.authCheckState()),
    onFetchUserInfo: (token, userId) => dispatch(actions.fetchUserInfo(token, userId)),
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(App));
