import { put } from 'redux-saga/effects';
import axios from '../../axios-odm';

import * as actions from '../actions/index';

export function* initializeWishlistSaga(action){
    try {
        let urlWishlist = "/userDetails/"+action.userId+"/wishlist";
        let response = yield axios.get(urlWishlist+".json");
        
        let wishlist = [];
        for( let key in response.data){
            wishlist.push({
                ...response.data[key],
                id: key
            });
        }
        // console.log(urlWishlist);
        // console.log(response);
        yield put(actions.initializeWishlistFinished(wishlist));  
    } catch (error) {
        console.log(error);
        yield put(actions.initializeCartFailed(error));
    }
}

export function* addProductToWishistSaga(action){
    yield put(actions.addProductToWishlistStart());
    try {
        let urlProduct = "/userDetails/"+action.userId+"/wishlist/"+action.productCode;
        let product = {
            unitPrice: action.unitPrice,
            oldPrice: action.oldPrice,
            productName: action.productName
        };
        yield axios.put(urlProduct+".json", product);  

        let urlWishlist= "/userDetails/"+action.userId+"/wishlist";
        let response = yield axios.get(urlWishlist+".json");
        let wishlist = [];
        for( let key in response.data){
            wishlist.push({
                ...response.data[key],
                id: key
            });
        }
        console.log(wishlist);
        yield put(actions.addProductToWishlistFinished(wishlist));  
    } catch (error) {
        console.log(error);
        yield put(actions.addProductToWishlistFailed());
    }
    // yield put(actions.addProductToWishlistFinished());
}

export function* deleteProductFromWishistSaga(action){
    yield put(actions.deleteProductFromWishlistStart());
    try {
        let urlProduct= "/userDetails/"+action.userId+"/wishlist/"+action.productCode;
        let resp = yield axios.delete(urlProduct+".json");
        // console.log(resp);
        let urlWishlist= "/userDetails/"+action.userId+"/wishlist";
        let response = yield axios.get(urlWishlist+".json");
        // console.log(response);

        let wishlist = [];
        for( let key in response.data){
            wishlist.push({
                ...response.data[key],
                id: key
            });
        }
        console.log(wishlist);

        yield put(actions.deleteProductFromWishlistFinished(wishlist));
    } catch (error) {
        console.log(error);
        yield put(actions.deleteProductFromWishlistFailed());
    }
    
}