import { put } from 'redux-saga/effects';
import axios from '../../axios-odm';

import * as actions from '../actions/index';

export function* fetchProductsSaga(action) {
    yield put(actions.fetchProductStart());
    const queryParams =
    // "?auth=" +
    // action.userId +
    '?orderBy="category"&equalTo="' +
    // "cooking_oil" +
    "detergent" +
    '"';
    try {
        let url = "/products";
        const response = yield axios.get( url+".json");
        // const response = yield axios.get( url+".json" + queryParams);
        const fetchedProducts = [];
        const fetchedCategories = [];
        fetchedCategories.push('All Products');
        for (let key in response.data) {
            fetchedProducts.push({
                ...response.data[key],
                id: key
            });
            // console.log(response.data[key].category);
            if(!fetchedCategories.includes(response.data[key].category))
                fetchedCategories.push(response.data[key].category);
            
        }
        // console.log(response.data);
        yield put(actions.fetchProductSuccess(fetchedProducts,fetchedCategories));
        // console.log(fetchedCategories);
    } catch (error) {
        console.log(error);
      yield put(actions.fetchProductFailed(error));
    }
}
