import React, {useState} from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';

import classes from './Toolbar.css';
import NavigationItems from '../NavigationItems/NavigationItems';
import DrawerToggle from '../Sidedrawer/DrawerToggle/DrawerToggle';
import Logo from '../../UI/Logo/Logo';
import InputForm from './SearchForm//InputForm/InputForm';
import Search from './SearchForm//Search/Search';
import NavigationItem from '../NavigationItems/NavigationItem/NavigationItem';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import * as actions from '../../../store/actions/index';


const Toolbar = (props) => {
    let searchText = '';
    const searchChangeHandler = (event) => {
        searchText = event.target.value;
        console.log(searchText);
        let searchedProducts = [];
        props.products.forEach(element => {
            if(element['productName'].toLowerCase().match(searchText.toLowerCase()) && searchText!=''){
                // console.log(element['productName']);
                searchedProducts.push(element);
            }
                // console.log(searchedProducts);
        });
        props.onSetSearchedProducts(searchedProducts);
        console.log(searchedProducts);
    }
    
    return (
        <header className={classes.Toolbar}>
        <DrawerToggle clicked={props.drawerToggleClicked} />
        <div className={classes.MobileOnly} onClick={props.logoIconClicked}>
            <Link to="/">
                <Logo/>
            </Link>
        </div>

        <div className={classes.SearchForm}>
            <div className={classes.SearchButton}>
                <Search onClick={props.searchClicked}/>
            </div>
            <div className={classes.InputForm}>
                <InputForm searchChangeHandler={searchChangeHandler}/>
            </div>
        </div>

        <div className={classes.RightGroup}>
            <nav className={classes.DesktopOnly}>
                <NavigationItems />
            </nav>
            <nav className={classes.Cart} onClick={props.cartClicked}>
                <NavigationItem link="/cart">
                    <ShoppingCartIcon color="inherit" className={classes.CartIcon}/>
                </NavigationItem>
                <p className={classes.CartUnits}>{props.cartContentNo}</p>
            </nav>
        </div>

    </header>
    )
};

const mapStateToProps = state => {
    return {
        cartContentNo: state.cart.cartContentNo,
        products: state.products.products,
        fetched: state.products.fetched,
    }
}
const mapDispatchToProps = dispatch => {
    return {
        onSetSearchedProducts: (products) => dispatch(actions.setSearchedProducts(products)),
    };
};
export default connect(mapStateToProps, mapDispatchToProps)(Toolbar);