import React, { useState } from 'react';
import { connect } from 'react-redux';

import classes from './DeliveryAddress.css';
import Input from '../../UI/Input/Input';
import { updateObject, checkValidity } from '../../../shared/utility';
import * as actions from '../../../store/actions/index';

const UserInfo = props => {

    const [deliveryAddressForm, setDeliveryAddressForm] = useState({
        // name: {
        //     elementType: 'input',
        //     elementConfig: {
        //         type: 'text',
        //         placeholder: 'Customer Name',
        //         label: 'Customer Name'
        //     },
        //     value: '',
        //     validation: {
        //         required: true
        //     },
        //     valid: false,
        //     touched: false
        // },
        addressName: {
            elementType: 'input',
            elementConfig: {
                type: 'text',
                placeholder: 'My Home Address',
                label: 'Address Name'
            },
            value: 'My Home Address',
            validation: {
                required: true,
            },
            valid: false,
            touched: false
        },
        email: {
            elementType: 'input',
            elementConfig: {
                type: 'email',
                placeholder: 'user-email@gmail.com',
                label: 'Your E-Mail'
            },
            value: 'kherkatarybhaskar@gmail.com',
            validation: {
                required: true,
                isEmail: true
            },
            valid: false,
            touched: false
        },
        phoneNo: {
            elementType: 'input',
            elementConfig: {
                type: 'text',
                placeholder: '9876543210',
                label: 'Contact Number'
            },
            value: '9876543210',
            validation: {
                required: true,
                minLength: 10,
                maxLength: 10,
                isNumeric: true
            },
            valid: false,
            touched: false
        },
        addressLine1: {
            elementType: 'input',
            elementConfig: {
                type: 'text',
                placeholder: '',
                label: 'House no, Building, Apartment'
            },
            value: 'Kherkatary Dera',
            validation: {
                required: true
            },
            valid: false,
            touched: false
        },
        addressLine2: {
            elementType: 'input',
            elementConfig: {
                type: 'text',
                placeholder: '',
                label: 'Area, Colony, Street, Sector, Village'
            },
            value: 'Patidoi Verela',
            validation: {
                required: true
            },
            valid: false,
            touched: false
        },
        landmark: {
            elementType: 'input',
            elementConfig: {
                type: 'text',
                placeholder: 'Hospital/Police Station etc',
                label: 'Landmark'
            },
            value: 'Bathou Mandir',
            validation: {
                required: true
            },
            valid: false,
            touched: false
        },
        pinCode: {
            elementType: 'input',
            elementConfig: {
                type: 'text',
                placeholder: '784506',
                label: 'Area PIN Code'
            },
            value: '784506',
            validation: {
                required: true,
                minLength: 5,
                maxLength: 6,
                isNumeric: true
            },
            valid: false,
            touched: false
        },
        city: {
            elementType: 'input',
            elementConfig: {
                type: 'text',
                placeholder: 'Missamari',
                label: 'City / Town'
            },
            value: 'Missamari',
            validation: {
                required: true
            },
            valid: false,
            touched: false
        },
    });

    const [formIsValid, setFormIsValid] = useState(true);
    props.onFetchDeliveryAddress(props.token, props.userId);
    
    console.log("PAGE RENDERED");

    const addAddressHandler = ( event ) => {
        event.preventDefault();

        const formData = {};
        for (let formElementIdentifier in deliveryAddressForm) {
            formData[formElementIdentifier] = deliveryAddressForm[formElementIdentifier].value;
        }
        console.log(formIsValid);
        console.log(formData);
        props.onAddDeliveryAddress(formData, props.token, props.userId);
        // const order = {
        //     ingredients: props.ings,
        //     price: props.price,
        //     orderData: formData,
        //     userId: props.userId
        // }        
        // props.onOrderBurger(order, props.token);

    }

    const inputChangedHandler = (event, inputIdentifier) => {
        const updatedFormElement = updateObject(deliveryAddressForm[inputIdentifier], {
            value: event.target.value,
            valid: checkValidity(event.target.value, deliveryAddressForm[inputIdentifier].validation),
            touched: true,
        });
        const updatedDeliveryAddressForm = updateObject(deliveryAddressForm, {
            [inputIdentifier]: updatedFormElement
        });

        let formIsValid = true;
        for (let inputIdentifier in updatedDeliveryAddressForm) {
            formIsValid = updatedDeliveryAddressForm[inputIdentifier].valid && formIsValid;
        }
        setDeliveryAddressForm(updatedDeliveryAddressForm);
        setFormIsValid(formIsValid);
    }

    const formElementsArray = [];
    for (let key in deliveryAddressForm) {
        formElementsArray.push({
            id: key,
            config: deliveryAddressForm[key]
        });
    }

    let form = (
        <form onSubmit={addAddressHandler}>
            {formElementsArray.map(formElement => (
                <Input 
                    key={formElement.id}
                    elementType={formElement.config.elementType}
                    elementConfig={formElement.config.elementConfig}
                    value={formElement.config.value}
                    invalid={!formElement.config.valid}
                    shouldValidate={formElement.config.validation}
                    touched={formElement.config.touched}
                    label={formElement.config.elementConfig.label}
                    changed={(event) => inputChangedHandler(event, formElement.id)} 
                />
            ))}
            <div className={classes.Button}>
                <button disabled={!formIsValid}>UPDATE</button>
            </div>
        </form>
    );
    return (
        <div className={classes.UserInfo}>
            <div className={classes.FormHolder}>
                {form}
            </div>
            {/* <div className={classes.Button}>
                <button disabled={formIsValid}>UPDATE</button>
            </div> */}
        </div>
    )
}

const mapStateToProps = state => {
    return {
        token: state.auth.token,
        userId: state.auth.userId,
        fetched: state.deliveryAddress.fetched
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onAddDeliveryAddress: (addressData,token,userId) => dispatch(actions.addDeliveryAddress(addressData,token,userId)),
        onFetchDeliveryAddress: (token,userId) => dispatch(actions.fetchDeliveryAddress(token,userId)),
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(UserInfo);