import React from 'react';

import classes from './ProductCard.css';

const ProductCard = (props) => {
    return (
        <div>
            <div className={classes.TitleHolder}>
                <div className={classes.TitleName}> {props.ProductName} </div>
                <div className={classes.TitleInfo}> {props.Units} </div>
                <div className={classes.TitleInfo}> {props.UnitPrice} </div>
                <div className={classes.TitleInfo}> {props.SubTotal} </div>
            </div>
        </div>
    )
}

export default ProductCard;
