export const deleteProductFromCart = (productCode, userId) => {
    return {
        type: actionTypes.DELETE_PRODUCT_FROM_CART,
        productCode: productCode,
        userId: userId
    }
}


export const updateCartContentNo = (cartContentNo) => {
    return{
        type: actionTypes.UPDATE_CART_CONTENT_NO,
        cartContentNo: cartContentNo
    }
}

export const orderProducts = (products, deliveryAddress, userId) => {
    return{
        type: actionTypes.ORDER_PRODUCTS,
        products: products,
        deliveryAddress: deliveryAddress,
        userId: userId
    }
}
export const orderProductsStart = () => {
    return{
        type: actionTypes.ORDER_PRODUCTS_START,
    }
}
export const orderProductsSuccess = () => {
    return{
        type: actionTypes.ORDER_PRODUCTS_SUCCESS,
    }
}
export const orderProductsFailed = () => {
    return{
        type: actionTypes.ORDER_PRODUCTS_FAILED,
    }
}