import React from 'react';
import { connect } from 'react-redux';

// import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import classes from './NavigationItems.css';
import NavigationItem from './NavigationItem/NavigationItem';

const navigationItems = props => (
    <ul className={classes.NavigationItems}>
        {/* <NavigationItem clicked={props.clicked} link="/categories" >Categories</NavigationItem> */}
        <NavigationItem clicked={props.clicked} link="/order-returns" >Orders</NavigationItem>
        <NavigationItem clicked={props.clicked} link="/wishlist" >Wishlist</NavigationItem>
        <NavigationItem clicked={props.clicked} link="/user" active>{ props.userName ? props.userName : 'Account'}</NavigationItem>
    </ul>
);

const mapStateToProps = state => {
    return {
        userName: state.auth.emailId
    }
}

export default connect(mapStateToProps)(navigationItems);
