import * as actionTypes from '../actions/actionTypes';
import { updateObject } from '../../shared/utility';

const initialState = {
    email: null,
    name: null,
    phoneNo: null,
    loading: false,
    fetched: false,
    error: null
};

const setUserInfo = ( state, action ) => {
    return updateObject( state, { loading: true } );
};
const setUserInfoSuccess = ( state, action ) => {
    return updateObject( state, { loading: false });
};
const fetchUserInfoStart = ( state, action ) => {
    return updateObject( state, { loading: true, error: null } );
};

const fetchUserInfoSuccess = ( state, action ) => {
    return updateObject( state, {
        email: action.email,
        name: action.name,
        phoneNo: action.phoneNo,
        loading: false,
        fetched: true,
    });
};

const fetchUserInfoFailed = ( state, action ) => {
    return updateObject( state, { loading: false, fetched: false, error: 'User Info not found. Add Info' } );
};

const reducer = ( state = initialState, action ) => {
    switch ( action.type ) {
        case actionTypes.SET_USER_INFO: return setUserInfo( state, action );
        case actionTypes.SET_USER_INFO_SUCCESS: return setUserInfoSuccess( state, action );
        case actionTypes.FETCH_USER_INFO_START: return fetchUserInfoStart( state, action );
        case actionTypes.FETCH_USER_INFO_SUCCESS: return fetchUserInfoSuccess( state, action );
        case actionTypes.FETCH_USER_INFO_FAILED: return fetchUserInfoFailed( state, action );
        default: return state;
    }
};

export default reducer;

