import { put } from 'redux-saga/effects';
import axios from '../../axios-odm';

import * as actions from '../actions/index';

export function* addDeliveryAddressSaga(action) {
    yield put(actions.addDeliveryAddressStart());
    try {
      let url = "/userDetails/"+action.userId+"/addresses";
    //   let url = "/userDetails/"+action.userId;
      const response = yield axios.put(
        url + ".json?auth=" + action.token,
        action.addressData
      );
      yield put(actions.addDeliveryAddressSuccess());
    } 
    catch (error) {
        yield put(actions.addDeliveryAddressFailed());

    }
}

export function* fetchDeliveryAddressSaga(action) {
    yield put(actions.fetchDeliveryAddressStart());
    try {
      let url = "/userDetails/"+action.userId+"/addresses";
    //   let url = "/userDetails/"+action.userId;
      const response = yield axios.get(
        url + ".json?auth=" + action.token
      );
    //   console.log(response.data);

        const formElementsArray = [];
        for (let key in response.data) {
            formElementsArray.push({
                id: key,
                value: response.data[key],
            });
        }
        // console.log(formElementsArray);
        // for (let key in formElementsArray) {
            // console.log(formElementsArray[key].id+ " : "+formElementsArray[key].value);
            // formElementsArray.map(formElementsArray[key].id);
        // }
    // console.log("fetchDeliveryAddressSaga");

    // for( let key in response.data ){
    //     console.log(key + " : "+ response.data[key]);
    // }
      yield put(actions.fetchDeliveryAddressSuccess(
            formElementsArray[0].value,
            formElementsArray[1].value,
            formElementsArray[2].value,
            formElementsArray[3].value,
            formElementsArray[4].value,
            formElementsArray[5].value,
            formElementsArray[6].value,
            formElementsArray[7].value
      ));
    } 
    catch (error) {
        yield put(actions.fetchDeliveryAddressFailed());
    }
}