import * as actionTypes from './actionTypes';

export const addDeliveryAddress = ( addressData, token, userId ) => {
    return {
        type: actionTypes.ADD_DELIVERY_ADDRESS,
        addressData: addressData,
        token: token,
        userId: userId
    }
}
export const addDeliveryAddressStart = () => {
    return {
        type: actionTypes.ADD_DELIVERY_ADDRESS_START,
    }
}
export const addDeliveryAddressSuccess = () => {
    return {
        type: actionTypes.ADD_DELIVERY_ADDRESS_SUCCESS,
    }
}
export const addDeliveryAddressFailed = error => {
    return {
        type: actionTypes.ADD_DELIVERY_ADDRESS_FAILED,
        error: error
    }
}

export const fetchDeliveryAddress = ( token, userId ) => {
    return {
        type: actionTypes.FETCH_DELIVERY_ADDRESS,
        token: token,
        userId: userId
    }
}
export const fetchDeliveryAddressStart = () => {
    return {
        type: actionTypes.FETCH_DELIVERY_ADDRESS_START,
    }
}
export const fetchDeliveryAddressSuccess = (addressLine1,addressLine2,addressName,city,email,landmark,phoneNo,pinCode) => {
    return {
        type: actionTypes.FETCH_DELIVERY_ADDRESS_SUCCESS,
        addressLine1: addressLine1,
        addressLine2: addressLine2,
        addressName: addressName,
        city: city,
        email: email,
        landmark: landmark,
        phoneNo: phoneNo,
        pinCode: pinCode
    }
}
export const fetchDeliveryAddressFailed = error => {
    return {
        type: actionTypes.FETCH_DELIVERY_ADDRESS_FAILED,
        error: error
    }
}