import React from 'react';
import { BrowserRouter } from 'react-router-dom';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware, compose, combineReducers } from 'redux';
import thunk from 'redux-thunk';
import createSagaMiddleware from 'redux-saga';

import './index.css';
import App from './App';
import authReducers from './store/reducers/auth';
import userInfoReducers from './store/reducers/userInfo';
import deliveryAddressReducers from './store/reducers/deliveryAddress';
import productReducers from './store/reducers/products';
import cartReducers from './store/reducers/cart';
import wishlistReducers from './store/reducers/wishlist';
import ordersReducers from './store/reducers/orders';
import { watchAuth, watchAccountControl, watchProductControl, watchOrderAndReturn } from './store/sagas/index';


import * as serviceWorker from './serviceWorker';

const composeEnhancers = process.env.NODE_ENV === 'development' ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ :null || compose ;

const rootReducer = combineReducers({
  auth: authReducers,
  userInfo: userInfoReducers,
  deliveryAddress: deliveryAddressReducers,
  products: productReducers,
  cart: cartReducers,
  wishlist: wishlistReducers,
  orders: ordersReducers
});

const sagaMiddleware = createSagaMiddleware();

const  store = createStore(rootReducer, composeEnhancers(
  applyMiddleware(thunk, sagaMiddleware)
));

sagaMiddleware.run(watchAuth);
sagaMiddleware.run(watchAccountControl);
sagaMiddleware.run(watchProductControl);
sagaMiddleware.run(watchOrderAndReturn);

ReactDOM.render(
  <Provider store={store}>
    <BrowserRouter>
      <App/>
    </BrowserRouter>
  </Provider>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
