import { put } from 'redux-saga/effects';
import axios from '../../axios-odm';
import moment from 'moment';

import * as actions from '../actions/index';

export function* fetchOrdersSaga(action) {
    console.log('FETCHING ORDERS');
    const queryParams =
    '?orderBy="userId"&equalTo="' +
    action.userId +
    '"';
    try {
        let urlOrders = "/orders";
        const response = yield axios.get(urlOrders+".json" + queryParams);
        // console.log(response);
        // const response = yield axios.get("/orders.json" + queryParams);
        const fetchedOrders = [];
        for (let key in response.data) {
            // fetchedOrders.push({
            //     ...response.data[key],
            //     id: key
            // });
            let amount = 0;
            for(let product in response.data[key]['products']){
                amount = amount + ( Number(response.data[key]['products'][product]['unitPrice']) * Number(response.data[key]['products'][product]['units']) );
            }

            fetchedOrders.push({
                ...response.data[key],
                amount: amount,
                id: key
            });

        }
        // for(let key in fetchedOrders){
        //     console.log(fetchedOrders[key]['time']);
        // }
        // fetchedOrders.sort( function(a,b) {
        //     var timeA = new Date(a.time);
        //     var timeB = new Date(b.time);
        //     // var timeA = moment(a.time, 'DD/MM/YYYY, HH:mm:ss');
        //     // var timeB = moment(b.time, 'DD/MM/YYYY, HH:mm:ss');
        //     // var timeA = new Date.parse(a.time);
        //     // var timeB = new Date.parse(b.time);
        //     // if(a.time < b.time)
        //     //     return 1;
        //     // if(a.time > b.time)
        //     //     return -1;   
        //     // return 0;
        //     if(timeA.getTime() < timeB.getTime())
        //         return -1;
        //     if(timeA.getTime() > timeB.getTime())
        //         return -1;   
        //     return 0;
        // });
        fetchedOrders.sort( function(a,b) {
            var timeA = moment(a.time, 'DD/MM/YYYY, HH:mm:ss');
            var timeB = moment(b.time, 'DD/MM/YYYY, HH:mm:ss');
          
            if(timeB.isAfter(timeA))
                return 1;
          
            if( timeB.isBefore(timeA))
                return -1;   
          
            return 0;
          });
        yield put(actions.fetchOrdersFinished(fetchedOrders));
        // console.log(fetchedOrders);


    } catch (error) {
        console.log(error);
        yield put(actions.initializeCartFailed(error));
    }
}