import React, { useEffect } from 'react';
import { connect } from 'react-redux';

import * as classes from './Cart.css';
import CartItem from './CartItem/CartItem';
import * as actions from '../../../store/actions/index';
import LoadingSpinner from '../../UI/LoadingSpinner/LoadingSpinner';


const Cart = props => {

    useEffect(() => {
        props.onInitializeCart(props.userId);
        if(props.cart != null){
            console.log('CART RESET');
            cartList = props.cart.map(cartItem => (
                <CartItem 
                productName={cartItem.productName}
                oldPrice={cartItem.oldPrice}
                unitPrice={cartItem.unitPrice}
                units={cartItem.units}
                productBarCode={cartItem.id} 
                />
            ))
        }
        console.log('Cart Updated');
        // console.log(props.cart);
    },[props.cartContentNo]);
    
    let cartList = <p>Cart Is Loading</p>;

    
    const orderButtonClickHandler = () => {
        const addressDetails = {
            addressLine1: props.addressLine1,
            addressLine2: props.addressLine2,
            city: props.city,
            email: props.email,
            landmark: props.landmark,
            phoneNo: props.phoneNo,
            pinCode: props.pinCode,
        }
        // console.log(props.cart);
        // console.log(addressDetails);
        props.onOrderProducts(props.cart, addressDetails, props.userId);
    }

    if(props.fetched && props.cart != null){
        cartList = props.cart.map(cartItem => (
            <CartItem 
            productName={cartItem.productName}
            oldPrice={cartItem.oldPrice}
            unitPrice={cartItem.unitPrice}
            units={cartItem.units}
            productBarCode={cartItem.id} 
            />
        ))

    }

    return (
        <div className={classes.CartHolder}>
            {cartList}
            <div className={classes.OrderButton} onClick={orderButtonClickHandler}>
                {/* <button onClick={orderButtonClickHandler}>Order Now</button> */}
                <p>Order Now</p>
            </div>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        userId: state.auth.userId,
        cart: state.cart.cart,
        fetched: state.cart.fetched,
        cartContentNo: state.cart.cartContentNo,
        addressLine1: state.deliveryAddress.addressLine1,
        addressLine2: state.deliveryAddress.addressLine2,
        city: state.deliveryAddress.city,
        email: state.deliveryAddress.email,
        landmark: state.deliveryAddress.landmark,
        phoneNo: state.deliveryAddress.phoneNo,
        pinCode: state.deliveryAddress.pinCode,
        // updating: state.cart.updating

    }
}
const mapDispatchToProps = dispatch => {
    return {
        onInitializeCart: (userId) => dispatch(actions.initializeCart(userId)),
        onOrderProducts: (products, deliveryAddress, userId) => dispatch(actions.orderProducts(products, deliveryAddress, userId)),
    };
};

export default connect(mapStateToProps,mapDispatchToProps)(Cart);