import React, {useEffect} from 'react';
import { connect } from 'react-redux';

import classes from './OrderAndReturns.css'
import * as actions from '../../../store/actions/index';
import OrderCard from './OrderCard/OrderCard';


const OrderAndReturns = (props) => {

    useEffect(() => {
        if(props.userId != null){
            props.onFetchOrders(props.userId);
        }
    },[]);

    let ordersList = <p>Orders are loading!!</p>
    if(props.fetched === true){
        ordersList = props.orders.map(orderItem => (
            <OrderCard 
                Products={orderItem.products}
                OrderID={orderItem.id}
                Amount={orderItem.amount}
                Time={orderItem.time}
                AddressLine1={orderItem.addressDetails.addressLine1}
                AddressLine2={orderItem.addressDetails.addressLine2}
                City={orderItem.addressDetails.city}
                Landmark={orderItem.addressDetails.landmark}
                EmailID={orderItem.addressDetails.email}
                PhoneNo={orderItem.addressDetails.phoneNo}
            />
        ))
    }
    
    return (
        <div className={classes.OrdersHolder}>
            {ordersList}
        </div>
    )
}


const mapStateToProps = state => {
    return {
        userId: state.auth.userId,
        orders: state.orders.orders,
        fetched: state.orders.fetched
        // updating: state.cart.updating
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onFetchOrders: (userId) => dispatch(actions.fetchOrders(userId)),
        // onAddProductToCart: (productName,productCode, unitPrice, oldPrice, userId) => dispatch(actions.addProductToCart(productName,productCode, unitPrice, oldPrice, userId)),
        // onRemoveProductFromCart: (productName,productCode, unitPrice, oldPrice, userId) => dispatch(actions.removeProductFromCart(productName,productCode, unitPrice, oldPrice, userId)),
        // onDeleteProductFromCart: (productCode, userId) => dispatch(actions.deleteProductFromCart(productCode, userId)),
    }
}
// export default connect(mapStateToProps, mapDispatchToProps)(CartItem);
export default connect(mapStateToProps, mapDispatchToProps)(OrderAndReturns);
