import * as actionTypes from '../actions/actionTypes';
import { updateObject } from '../../shared/utility';

const initialState = {
    products: [],
    categories: [],
    loading: false,
    error: null,
    fetched: false,
    searchedProducts: []
};

const fetchProductStart = ( state, action ) => {
    return updateObject( state, { loading: true } );
};

const fetchProductFailed = ( state, action ) => {
    return updateObject( state, { loading: false, error: action.error } );
};

const fetchProductSuccess = ( state, action ) => {
    return updateObject( state, {
        products: action.products,
        categories: action.categories,
        loading: false,
        error: null,
        fetched: true
    });
};

const setSearchedProducts = ( state, action ) => {
    return updateObject( state, {
        searchedProducts: action.searchedProducts
    });
}

const reducer = ( state = initialState, action ) => {
    switch ( action.type ) {
        case actionTypes.FETCH_PRODUCT_SUCCESS: return fetchProductSuccess( state, action );
        case actionTypes.FETCH_DELIVERY_ADDRESS_START: return fetchProductStart( state, action );
        case actionTypes.FETCH_DELIVERY_ADDRESS_FAILED: return fetchProductFailed( state, action );
        case actionTypes.SET_SEARCHED_PRODUCTS: return setSearchedProducts( state, action );
        default: return state;
    }
};

export default reducer;