import { takeEvery } from "redux-saga/effects";

import * as actionTypes from "../actions/actionTypes";

import {
    authUserSaga,
    checkAuthTimeoutSaga,
    logoutSaga,
    authCheckStateSaga
} from "./auth";

import {
    setUserInfoSaga,
    fetchUserInfoSaga
} from "./userInfo";

import {
    addDeliveryAddressSaga,
    fetchDeliveryAddressSaga
} from "./deliveryAddress";

import { addProductSaga } from "./addProduct";

import { fetchProductsSaga } from "./products";

import { 
    initializeCartSaga,
    addProductToCartSaga,
    removeProductFromCartSaga,
    deleteProductFromCartSaga,
    orderProductsSaga,
} from "./cart";

import { 
    addProductToWishistSaga,
    deleteProductFromWishistSaga,
    initializeWishlistSaga
} from "./wishlist";

import { 
    fetchOrdersSaga,
} from "./orders";

export function* watchAuth() {
    yield takeEvery(actionTypes.AUTH_CHECK_TIMEOUT, checkAuthTimeoutSaga);
    yield takeEvery(actionTypes.AUTH_USER, authUserSaga);
    yield takeEvery(actionTypes.AUTH_CHECK_STATE, authCheckStateSaga);
    yield takeEvery(actionTypes.AUTH_LOGOUT, logoutSaga);
}

export function* watchAccountControl() {
    yield takeEvery(actionTypes.SET_USER_INFO, setUserInfoSaga);
    yield takeEvery(actionTypes.FETCH_USER_INFO, fetchUserInfoSaga);
    yield takeEvery(actionTypes.ADD_DELIVERY_ADDRESS, addDeliveryAddressSaga);
    yield takeEvery(actionTypes.FETCH_DELIVERY_ADDRESS, fetchDeliveryAddressSaga);
}

export function* watchProductControl(){
    yield takeEvery(actionTypes.ADD_PRODUCT, addProductSaga);
    yield takeEvery(actionTypes.FETCH_PRODUCT, fetchProductsSaga);

    yield takeEvery(actionTypes.INITIALIZE_CART, initializeCartSaga);
    yield takeEvery(actionTypes.ADD_PRODUCT_TO_CART, addProductToCartSaga);
    yield takeEvery(actionTypes.REMOVE_PRODUCT_FROM_CART, removeProductFromCartSaga);
    yield takeEvery(actionTypes.DELETE_PRODUCT_FROM_CART, deleteProductFromCartSaga);


    yield takeEvery(actionTypes.INITIALIZE_WISHLIST, initializeWishlistSaga);
    yield takeEvery(actionTypes.ADD_PRODUCT_TO_WISHLIST, addProductToWishistSaga);
    yield takeEvery(actionTypes.DELETE_PRODUCT_FROM_WISHLIST, deleteProductFromWishistSaga);
    yield takeEvery(actionTypes.ORDER_PRODUCTS, orderProductsSaga);
}

export function* watchOrderAndReturn(){
    yield takeEvery(actionTypes.FETCH_ORDERS, fetchOrdersSaga);
}