import * as actionTypes from '../actions/actionTypes';
import { updateObject } from '../../shared/utility';

const initialState = {
    cart: null,
    cartContentNo: 0,
    error: null,
    fetched: false,
    updating: false
};


const initializeCartSuccess = ( state, action ) => {
    return updateObject( state, {
        cart: action.cart,
        loading: false,
        error: null,
        fetched: true
    });
};

const initializeCartFailed = ( state, action ) => {
    return updateObject( state, {
        cart: null,
        error: null,
    });
};
const updateCartContentNo = (state, action) => {
    return updateObject(state, {
        cartContentNo: action.cartContentNo
    });
}
const addProductToCartStart = (state, action) => {
    return updateObject(state, {
        updating: true
    });
}
const addProductToCartFinished = (state, action) => {
    return updateObject(state, {
        updating: false
    });
}
const addProductToCartFailed = (state, action) => {
    return updateObject(state, {
        updating: false
    });
}
export const removeProductFromCartStart = (state, action) => {
    return updateObject(state, {
        updating: true
    });
}
export const removeProductFromCartFinished = (state, action) => {
    return updateObject(state, {
        updating: false
    });
}
export const removeProductFromCartFailed = (state, action) => {
    return updateObject(state, {
        updating: false
    });
}
export const deleteProductFromCartStart = (state, action) => {
    return updateObject(state, {
        updating: true
    });
}
export const deleteProductFromCartFinished = (state, action) => {
    return updateObject(state, {
        updating: false
    });
}
export const deleteProductFromCartFailed = (state, action) => {
    return updateObject(state, {
        updating: false
    });
}

