const authLogout = (state, action) => {
    return updateObject(state, { 
        cart: null,
        cartContentNo: 0,
        error: null,
        fetched: false,
        updating: false
    });
};

const reducer = ( state = initialState, action ) => {
    switch ( action.type ) {
        case actionTypes.INITIALIZE_CART_SUCCESS: return initializeCartSuccess( state, action );
        case actionTypes.INITIALIZE_CART_FAILED: return initializeCartFailed( state, action );
        case actionTypes.UPDATE_CART_CONTENT_NO: return updateCartContentNo( state, action );
        
        case actionTypes.ADD_PRODUCT_TO_CART_START: return addProductToCartStart( state, action );
        case actionTypes.ADD_PRODUCT_TO_CART_FINISHED: return addProductToCartFinished( state, action );
        case actionTypes.ADD_PRODUCT_TO_CART_FAILED: return addProductToCartFailed( state, action );
        
        case actionTypes.REMOVE_PRODUCT_FROM_CART_START: return removeProductFromCartStart( state, action );
        case actionTypes.REMOVE_PRODUCT_FROM_CART_FINISHED: return removeProductFromCartFinished( state, action );
        case actionTypes.REMOVE_PRODUCT_FROM_CART_FAILED: return removeProductFromCartFailed( state, action );
        
        case actionTypes.DELETE_PRODUCT_FROM_CART_START: return deleteProductFromCartStart( state, action );
        case actionTypes.DELETE_PRODUCT_FROM_CART_FINISHED: return deleteProductFromCartFinished( state, action );
        case actionTypes.DELETE_PRODUCT_FROM_CART_FAILED: return deleteProductFromCartFailed( state, action );
        
        case actionTypes.AUTH_LOGOUT: return authLogout( state, action );

        // case actionTypes.ADD_PRODUCT_TO_WISHLIST_START: return addProductToWishlistStart( state, action );
        // case actionTypes.ADD_PRODUCT_TO_WISHLIST_FINISHED: return addProductToWishlistFinished( state, action );
        // case actionTypes.ADD_PRODUCT_TO_WISHLIST_FAILED: return addProductToWishlistFailed( state, action );
        // case actionTypes.DELETE_PRODUCT_FROM_WISHLIST_START: return deleteProductFromWishlistStart( state, action );
        // case actionTypes.DELETE_PRODUCT_FROM_WISHLIST_FINISHED: return deleteProductFromWishlistFinished( state, action );
        // case actionTypes.DELETE_PRODUCT_FROM_WISHLIST_FAILED: return deleteProductFromWishlistFailed( state, action );

        default: return state;
    }
};

export default reducer;