import * as actionTypes from './actionTypes';

export const initializeWishlist = (userId) => {
    return {
        type: actionTypes.INITIALIZE_WISHLIST,
        userId: userId
    }
}
export const initializeWishlistFinished = (wishlist) => {
    return {
        type: actionTypes.INITIALIZE_WISHLIST_FINISHED,
        wishlist: wishlist
    }
}
export const initializeWishlistFailed = () => {
    return {
        type: actionTypes.INITIALIZE_WISHLIST_FAILED,
    }
}

export const addProductToWishlistStart = () => {
    return {
        type: actionTypes.ADD_PRODUCT_TO_WISHLIST_START,
    }
}
export const addProductToWishlistFinished = (wishlist) => {
    return {
        type: actionTypes.ADD_PRODUCT_TO_WISHLIST_FINISHED,
        wishlist: wishlist
    }
}
export const addProductToWishlistFailed = () => {
    return {
        type: actionTypes.ADD_PRODUCT_TO_WISHLIST_FAILED,
    }
}

export const addProductToWishlist = (productName,productCode, unitPrice, oldPrice, userId) => {
    return {
        type: actionTypes.ADD_PRODUCT_TO_WISHLIST,
        productName: productName,
        productCode: productCode,
        unitPrice: unitPrice,
        oldPrice: oldPrice,
        userId: userId
    }
}
export const deleteProductFromWishlistStart = () => {
    return {
        type: actionTypes.DELETE_PRODUCT_FROM_WISHLIST_START,
    }
}
export const deleteProductFromWishlistFinished = (wishlist) => {
    return {
        type: actionTypes.DELETE_PRODUCT_FROM_WISHLIST_FINISHED,
        wishlist: wishlist
    }
}
export const deleteProductFromWishlistFailed = () => {
    return {
        type: actionTypes.DELETE_PRODUCT_FROM_WISHLIST_FAILED,
    }
}
export const deleteProductFromWishlist = (productCode, userId) => {
    return {
        type: actionTypes.DELETE_PRODUCT_FROM_WISHLIST,
        productCode: productCode,
        userId: userId
    }
}
