import * as actionTypes from '../actions/actionTypes';
import { updateObject } from '../../shared/utility';

const initialState = {
    addressLine1: null,
    addressLine2: null,
    addressName: null,
    city: null,
    email: null,
    landmark: null,
    phoneNo: null,
    pinCode: null,
    loading: false,
    fetched: false,
};

const addDeliveryAddressStart = ( state, action ) => {
    return updateObject( state, { loading: true } );
};
const addDeliveryAddressSuccess = ( state, action ) => {
    return updateObject( state, { loading: false });
};
const addDeliveryAddressFailed = ( state, action ) => {
    return updateObject( state, { loading: false } );
};

const fetchDeliveryAddressStart = ( state, action ) => {
    return updateObject( state, { loading: true } );
};
const fetchDeliveryAddressSuccess = ( state, action ) => {
    return updateObject( state, { 
        loading: false,
        fetched: true,
        addressLine1: action.addressLine1,
        addressLine2: action.addressLine2,
        addressName: action.addressName,
        city: action.city,
        email: action.email,
        landmark: action.landmark,
        phoneNo: action.phoneNo,
        pinCode: action.pinCode, 
    });
};
const fetchDeliveryAddressFailed = ( state, action ) => {
    return updateObject( state, { loading: false } );
};

const authLogout = () => {
    return {
        addressLine1: null,
        addressLine2: null,
        addressName: null,
        city: null,
        email: null,
        landmark: null,
        phoneNo: null,
        pinCode: null,
        loading: false,
        fetched: false,
    };
};

const reducer = ( state = initialState, action ) => {
    switch ( action.type ) {
        case actionTypes.ADD_DELIVERY_ADDRESS_START: return addDeliveryAddressStart( state, action );
        case actionTypes.ADD_DELIVERY_ADDRESS_SUCCESS: return addDeliveryAddressSuccess( state, action );
        case actionTypes.ADD_DELIVERY_ADDRESS_FAILED: return addDeliveryAddressFailed( state, action );

        case actionTypes.FETCH_DELIVERY_ADDRESS_START: return fetchDeliveryAddressStart( state, action );
        case actionTypes.FETCH_DELIVERY_ADDRESS_SUCCESS: return fetchDeliveryAddressSuccess( state, action );
        case actionTypes.FETCH_DELIVERY_ADDRESS_FAILED: return fetchDeliveryAddressFailed( state, action );
        
        case actionTypes.AUTH_LOGOUT: return authLogout( state, action );
        default: return state;
    }
};

export default reducer;

