import React,{ useState, useEffect } from 'react';
import { connect } from 'react-redux';

import classes from './Layout.css';
import Aux from '../../hoc/Aux';
import Toolbar from '../Navigation/Toolbar/Toolbar';
import Sidedrawer from '../Navigation/Sidedrawer/Sidedrawer';
import LoadingSpinner from '../UI/LoadingSpinner/LoadingSpinner';


const Layout = props => {

    const [showSideDrawer, setShowSideDrawer] = useState(false);

    const sideDrawerClosedHandler = () => {
        setShowSideDrawer(false);
    }

    const siderDrawerToggleHandler = () => {
        setShowSideDrawer(true)
        setShowSideDrawer(!showSideDrawer);
    }

    const cartClickHandler = () => {
        console.log("CART CLICKED!!!!!")
    }

    const searchClickedHandler = () => {
        console.log("SEARCH CLICKED!!!!!")
    }

    const logoClickHandler = () => {
        console.log("LOGO CLICKED!!!!!")
    }
    // console.log("LAYOUT RENDERED!!!!!")

    return (
        <Aux>
            
            <Toolbar drawerToggleClicked={siderDrawerToggleHandler} logoIconClicked={logoClickHandler} cartClicked={cartClickHandler} searchClicked={searchClickedHandler}/>
            <Sidedrawer open={showSideDrawer} closed={sideDrawerClosedHandler} />
            <div >
                { props.cartUpdating || props.wishlistUpdating || props.authLoading ? 
                    <div className={classes.SpinnerDiv}>
                        <LoadingSpinner/>
                    </div>
                : null}
            </div>
            <main className={classes.Content}>
            {/* <p>Space for Category Selection</p> */}
                {props.children}
                
            </main>
        </Aux>
    );
}

const mapStateToProps = state => {
    return {
        cartUpdating: state.cart.updating,
        wishlistUpdating: state.wishlist.updating,
        authLoading: state.auth.loading
    }
}

export default connect(mapStateToProps,)(Layout);