import React from 'react';
import { connect } from 'react-redux';

import * as actions from '../../../../store/actions/index';
import AttaImage from '../../../../assets/attaimage.png';
import classes from './ProductCard.css';

const ProductCard = (props) => {

    const AddToCartClickHandler = () => {
        props.onAddProductToCart(props.productName, props.productBarCode, props.newPrice, props.retailPrice, props.userId);
    }
    const AddToWishlistClickHandler = () => {
        props.onAddProductToWishlist(props.productName, props.productBarCode, props.newPrice, props.retailPrice, props.userId);
    }

    return (
        <div className={classes.ProductCardHolder}>
            <div className={classes.ImageHolder}>
                <img src={AttaImage} className={classes.Image}/>
            </div>
            <div className={classes.InfoSection}>
                 {/* <hr></hr> */}
                <span>
                    <p className={classes.ProductName}>{props.productName}</p>
                    <hr></hr>
                    <p className={classes.ProductQuantity}>Unit Quantity: {props.quantity}</p>
                </span>
                <div className={classes.PriceText}>
                    <p className={classes.OldPriceText}>₹{props.retailPrice}</p>
                    <p className={classes.NewPriceText}>₹{props.newPrice}</p>
                </div>
            </div>
            <div className={classes.ButtonHolder}>
                <button onClick={AddToCartClickHandler}>Add To Cart</button>
                <button onClick={AddToWishlistClickHandler}>Add To Wishlist</button>
            </div>
        </div>
    )
}
const mapStateToProps = state => {
    return {
        userId: state.auth.userId,
    }
}
const mapDispatchToProps = dispatch => {
    return {
        onAddProductToCart: (productName, productCode, unitPrice, oldPrice, userId) => dispatch(actions.addProductToCart(productName, productCode, unitPrice, oldPrice, userId)),
        onAddProductToWishlist: (productName, productCode, unitPrice, oldPrice, userId) => dispatch(actions.addProductToWishlist(productName, productCode, unitPrice, oldPrice, userId)),
        // onDeleteProductFromWishlist: (productCode, userId) => dispatch(actions.deleteProductFromWishlist(productName, productCode, unitPrice, oldPrice, userId))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ProductCard);
