import * as actionTypes from './actionTypes';

export const fetchProduct = (userId) => {
    return {
        type: actionTypes.FETCH_PRODUCT,
        userId: userId,
    }
}
export const fetchProductStart = () => {
    return {
        type: actionTypes.FETCH_PRODUCT_START,
    }
}
export const fetchProductSuccess = (products, categories) => {
    return {
        type: actionTypes.FETCH_PRODUCT_SUCCESS,
        products: products,
        categories: categories
    }
}
export const fetchProductFailed = () => {
    return {
        type: actionTypes.FETCH_PRODUCT_FAILED,
    }
}

export const setSearchedProducts = (searchedProducts) => {
    return {
        type: actionTypes.SET_SEARCHED_PRODUCTS,
        searchedProducts: searchedProducts
    }
}
