import { put } from 'redux-saga/effects';
import axios from '../../axios-odm';

import * as actions from '../actions/index';

export function* setUserInfoSaga(action) {
    try {
      let url = "/userDetails/"+action.userId;
      yield axios.put(
        url+".json?auth=" + action.token,
        action.userInfo
      );
      yield put(actions.setUserInfoSuccess());
    } 
    catch (error) {
    }
}

export function* fetchUserInfoSaga(action) {
    yield put(actions.fetchUserInfoStart());
    // const queryParams =
    //   "?auth=" +
    //   action.token +
    //   '&orderBy="userId"&equalTo="' +
    //   action.userId +
    //   '"';
    try {
      let url = "/userDetails/"+action.userId+"/userdata";
      const response = yield axios.get( url+".json");

      yield put(actions.fetchUserInfoSuccess(response.data["email"],response.data["name"],response.data["phoneNo"]));
    } catch (error) {
        console.log(error);
      yield put(actions.fetchUserInfoFailed(error));
    }
  }
