import React from 'react';
import { connect } from 'react-redux';


import classes from './OrderCard.css';
import ProductCard from '../ProductCard/ProductCard';

const OrderCard = (props) => {

    let productList = <p>Products are loading!!</p>
    if(props.fetched === true){
        for( let key in props.orders ){
            // console.log( props.Products)
            productList = props.Products.map(product => (
                <ProductCard 
                    ProductName={product.productName}
                    UnitPrice={product.unitPrice}
                    Units={product.units}
                    SubTotal={Number(product.unitPrice)*Number(product.units)}
                />
            ))
            // for(let product in props.orders[key]['products']){
            //     console.log( props.orders[key]['products'][product])
            // }
        }
    }

    return (
        <div className={classes.Holder}>
            <div className={classes.InfoHolder}>
                <p><b>Order Placed on : </b> {props.Time}</p>
                <p><b>Order Status : </b> In transit</p>
                <p><b>Order Amount : </b>₹ {props.Amount}/-</p>
            </div>
            
            <div className={classes.AddressTitle}>Products List</div>
            <div className={classes.TitleHolder}>
                <div className={classes.TitleName}> Product Name </div>
                <div className={classes.TitleInfo}> Units </div>
                <div className={classes.TitleInfo}> Unit Price </div>
                <div className={classes.TitleInfo}> Sub Total </div>
            </div>
            {/* <ProductCard/> */}
            {productList}
            <div className={classes.AddressHolder}>
                <div className={classes.AddressTitle}> Delivery Details</div>
                <div>Address Line 1 : {props.AddressLine1}</div>
                <div>Address Line 2 : {props.AddressLine2}</div>
                <div>City : {props.City}</div>
                <div>Landmark : {props.Landmard}</div>
                <div>EmailID : {props.EmailID}</div>
                <div>Phone No : {props.PhoneNo}</div>
            </div>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        orders: state.orders.orders,
        fetched: state.orders.fetched
        // updating: state.cart.updating
    }
}
export default connect(mapStateToProps)(OrderCard);
