import * as actionTypes from '../actions/actionTypes';
import { updateObject } from '../../shared/utility';

const initialState = {
    orders: [],
    loading: false,
    error: null,
    fetched: false
};

const fetchOrders = ( state, action ) => {
    return updateObject( state, { loading: true } );
};

const fetchOrdersFailed = ( state, action ) => {
    return updateObject( state, { loading: false, error: action.error } );
};

const fetchOrdersFinished = ( state, action ) => {
    return updateObject( state, {
        orders: action.orders,
        loading: false,
        error: null,
        fetched: true
    });
};

const reducer = ( state = initialState, action ) => {
    switch ( action.type ) {
        case actionTypes.FETCH_ORDERS: return fetchOrders( state, action );
        case actionTypes.FETCH_ORDERS_FINISHED: return fetchOrdersFinished( state, action );
        case actionTypes.FETCH_ORDERS_FAILED: return fetchOrdersFailed( state, action );
        default: return state;
    }
};

export default reducer;