//         type: actionTypes.SET_USER_INFO_SUCCESS,
//         userInfo: userInfo,
//         token: token,
//         userId: id
//     }
// }
export const setUserInfoSuccess = () => {
    return {
        type: actionTypes.SET_USER_INFO_SUCCESS,
    }
}

export const fetchUserInfo = ( token, id ) => {
    return {
        type: actionTypes.FETCH_USER_INFO,
        token: token,
        userId: id
    }
}

export const fetchUserInfoSuccess = ( email, name, phoneNo ) => {
    return {
      type: actionTypes.FETCH_USER_INFO_SUCCESS,
      email: email,
      name: name,
      phoneNo: phoneNo
    };
};

export const fetchUserInfoFailed = error => {
    return {
        type: actionTypes.FETCH_USER_INFO_FAILED,
        error: error
    };
};

export const fetchUserInfoStart = () => {
    return {
        type: actionTypes.FETCH_USER_INFO_START
    };
};