import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';

import * as actions from '../../../store/actions/index';
import ProductCard from './ProductCard/ProductCard';
import CategorySelection from './CategorySelection/CategorySelection';
import classes from './AllProducts.css';
import LoadingSpinner from '../../UI/LoadingSpinner/LoadingSpinner';


const AllProducts = props => {

    const [productCategory, setProductCategory] = useState('All Products');
    
    // let productsList = <p>Products are loading. Please wait.</p>;
    let productsList = null;
    let spinner = <LoadingSpinner/>;
    let filteredProducts = null;
    let filtered = null

    useEffect(() => {
        props.onFetchProduct();
        // console.log(productsList);
    },[]);

    let categoryOptions = null;

    if(props.error!==null){
        productsList = <p>{props.error}</p>;
    }
    if(props.fetched){
        if(props.searchedProducts.length > 0){
            productsList = props.searchedProducts.map(product => (
                <ProductCard 
                    productName={product.productName}
                    retailPrice={product.mrp}
                    newPrice={product.onlinedailymarketPrice}
                    quantity={product.unitQuantity}
                    productBarCode={product.id}
                    key={product.id}
                />
            ))
        }
        else if(productCategory === 'All Products'){
            productsList = props.products.map(product => (
                <ProductCard 
                    productName={product.productName}
                    retailPrice={product.mrp}
                    newPrice={product.onlinedailymarketPrice}
                    quantity={product.unitQuantity}
                    productBarCode={product.id}
                    key={product.id}
                />
            ))
        }
        else{
            filtered = props.products.filter(product => product.category === productCategory);
            productsList = filtered.map(product => (
                <ProductCard 
                    productName={product.productName}
                    retailPrice={product.mrp}
                    newPrice={product.onlinedailymarketPrice}
                    quantity={product.unitQuantity}
                    productBarCode={product.id}
                    key={product.id}
                />
            ))
        }

        categoryOptions = props.categories.map(category => (
            <div>
                <CategorySelection
                    categoryName={category}
                    clicked={() => onCategorySelectClicked(category)}
                />
            </div>
        ))        
    }

    const onCategorySelectClicked = (categoryName) => {
        setProductCategory(categoryName);
    }

    return (
        <div className={classes.AllProductsHolder}>
            {/* {spinner} */}
            <div className={classes.CatrgorySelection}>
                {categoryOptions}
            </div>
            {productsList}
        </div>
    )
}

const mapStateToProps = state => {
    return {
        products: state.products.products,
        searchedProducts: state.products.searchedProducts,
        categories: state.products.categories,
        loading: state.products.loading,
        error: state.products.error,
        fetched: state.products.fetched,
        userId: state.auth.userId
    }
}
const mapDispatchToProps = dispatch => {
    return {
      onFetchProduct: () => dispatch(actions.fetchProduct()),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(AllProducts);