import * as actionTypes from '../actions/actionTypes';
import { updateObject } from '../../shared/utility';

const initialState = {
    wishlist: null,
    updating: false,
    wishlistUpdated: false,
    error: null
};

const initializeWishlistFinished = ( state, action ) => {
    return updateObject( state, {
        wishlist: action.wishlist,
        updating: false,
        wishlistUpdated: true,
        error: false
    });
};

const initializeWishlistFailed = ( state, action ) => {
    return updateObject( state, {
        wishlistUpdated: false,
        error: action.error
    });
};
const addProductToWishlistStart = (state, action) => {
    return updateObject(state, {
        updating: true
    });
}
const addProductToWishlistFinished = (state, action) => {
    return updateObject(state, {
        wishlist: action.wishlist,
        cartUpdated: action.cartUpdated,
        updating: false
    });
}
const addProductToWishlistFailed = (state, action) => {
    return updateObject(state, {
        updating: false
    });
}
const deleteProductFromWishlistStart = (state, action) => {
    return updateObject(state, {
        updating: true
    });
}
const deleteProductFromWishlistFinished = (state, action) => {
    return updateObject(state, {
        wishlist: action.wishlist,
        cartUpdated: action.cartUpdated,
        updating: false
    });
}
const deleteProductFromWishlistFailed = (state, action) => {
    return updateObject(state, {
        updating: false
    });
}

const authLogout = () => {
    return {
        wishlist: null,
        updating: false,
        wishlistUpdated: false,
        error: null
    };
};

const reducer = ( state = initialState, action ) => {
    switch ( action.type ) {
        case actionTypes.INITIALIZE_WISHLIST_FINISHED: return initializeWishlistFinished( state, action );
        case actionTypes.INITIALIZE_WISHLIST_FAILED: return initializeWishlistFailed( state, action );

        case actionTypes.ADD_PRODUCT_TO_WISHLIST_START: return addProductToWishlistStart( state, action );
        case actionTypes.ADD_PRODUCT_TO_WISHLIST_FINISHED: return addProductToWishlistFinished( state, action );
        case actionTypes.ADD_PRODUCT_TO_WISHLIST_FAILED: return addProductToWishlistFailed( state, action );
        case actionTypes.DELETE_PRODUCT_FROM_WISHLIST_START: return deleteProductFromWishlistStart( state, action );
        case actionTypes.DELETE_PRODUCT_FROM_WISHLIST_FINISHED: return deleteProductFromWishlistFinished( state, action );
        case actionTypes.DELETE_PRODUCT_FROM_WISHLIST_FAILED: return deleteProductFromWishlistFailed( state, action );
        
        case actionTypes.AUTH_LOGOUT: return authLogout( state, action );
        default: return state;
    }
};

export default reducer;