import React, { useEffect } from 'react';
import { connect } from 'react-redux';

import * as classes from './CartItem.css';
import AttaImage from '../../../../assets/attaimage.png';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle';
import DeleteIcon from '@material-ui/icons/Delete';
import * as actions from '../../../../store/actions/index';
import LoadingSpinner from '../../../UI/LoadingSpinner/LoadingSpinner';

const CartItem = props => {

    const AddToCartHandler = () => {
        props.onAddProductToCart(props.productName, props.productBarCode, props.unitPrice, props.oldPrice, props.userId);
    }
    const RemoveFromCartHandler = () => {
        props.onRemoveProductFromCart(props.productName, props.productBarCode, props.unitPrice, props.oldPrice, props.userId);
    }
    const DeleteFromCartHandler = () => {
        props.onDeleteProductFromCart(props.productBarCode, props.userId);
    }

    let subTotalCost = Number(props.unitPrice) * Number(props.units);
    

    return (
        <div className={classes.CartItemHolder}>

            <div className={classes.ProductInfoViewHolder}>

                <div className={classes.InfoControlHolder}>

                    <div className={classes.ProductTitleHolder}>
                        <div className={classes.ProductNameHolder}>
                            <p>{props.productName}</p>
                        </div>
                    </div>
                    <div className={classes.InfoViewHolder}>

                        <div className={classes.InfoTitleViewHolder}>
                            <div className={classes.TitleHolder}>
                                <p className={classes.Title}>Unit Price</p>
                            </div>
                            <div className={classes.TitleHolder}>
                                <p className={classes.Title}>Units</p>
                            </div>
                            <div className={classes.TitleHolder}>
                                <p className={classes.Title}>Sub-Total</p>  
                            </div>
                        </div>
                        <div className={classes.InfoDetailViewHolder}>
                            <div className={classes.TitleHolder}>
                            <p className={classes.Values}><span className={classes.OldPriceText}>₹{props.oldPrice}</span> ₹{props.unitPrice}</p>
                            </div>
                            <div className={classes.TitleHolder}>
                                <p className={classes.Values}>x{props.units}</p>
                            </div>
                            <div className={classes.TitleHolder}>
                                <p className={classes.Values}>{subTotalCost}</p>  
                            </div>
                        </div>
                    </div>
                    <div className={classes.CartControlViewHolder}>
                        {/* <div className={classes.ControlHolder} onClick={AddToCartClickHandler(props.productName, props.productBarCode, props.unitPrice, props.oldPrice, props.userId)}> */}
                        <div className={classes.ControlHolder} onClick={AddToCartHandler}>
                            <AddCircleIcon/>
                            {/* { !props.updating ? <AddCircleIcon/> : <LoadingSpinner/>} */}
                        </div>
                        <div className={classes.ControlHolder} onClick={RemoveFromCartHandler}>
                            <RemoveCircleIcon/>
                        </div>
                        <div className={classes.ControlHolder} onClick={DeleteFromCartHandler}>
                        {/* { !props.updating ? <DeleteIcon/> : <LoadingSpinner/>} */}
                            <DeleteIcon/>
                        </div>
                    </div>
                </div>
                
                <div className={classes.ProducImageViewHolder}>
                    <div className={classes.ProducImageHolder}>
                        <img src={AttaImage} className={classes.Image}/>
                    </div>
                </div>
                
            </div>

        </div>
    )
}

const mapStateToProps = state => {
    return {
        userId: state.auth.userId,
        // updating: state.cart.updating
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onInitializeCart: (userId) => dispatch(actions.initializeCart(userId)),
        onAddProductToCart: (productName,productCode, unitPrice, oldPrice, userId) => dispatch(actions.addProductToCart(productName,productCode, unitPrice, oldPrice, userId)),
        onRemoveProductFromCart: (productName,productCode, unitPrice, oldPrice, userId) => dispatch(actions.removeProductFromCart(productName,productCode, unitPrice, oldPrice, userId)),
        onDeleteProductFromCart: (productCode, userId) => dispatch(actions.deleteProductFromCart(productCode, userId)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(CartItem);